@font-face {
  font-family: "CodecProExtraBold";
  src: local("CodecPro"),
      local("CodecPro-ExtraBold"),
      url("../fonts/CodecPro-ExtraBold/CodecProExtraBold.woff") format("woff"),
      url("../fonts/CodecPro-ExtraBold/CodecProExtraBold.eot?#iefix") format("embedded-opentype"),
      url("../fonts/CodecPro-ExtraBold/CodecProExtraBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 900;
  font-display: swap;
}

